"use client"

import { Button } from "@/components/ui/button"
import { Facebook, Apple } from "lucide-react"

// Create a custom TikTok icon since Lucide might not have it or it's named differently
const TikTokIcon = ({ className }: { className?: string }) => (
    <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        strokeWidth="2"
        strokeLinecap="round"
        strokeLinejoin="round"
        className={className}
    >
        <path d="M9 12a4 4 0 1 0 4 4V4a5 5 0 0 0 5 5" />
    </svg>
)

interface SocialLoginButtonsProps {
    onLogin: (provider: string) => void;
}

export function SocialLoginButtons({ onLogin }: SocialLoginButtonsProps) {
    return (
        <div className="grid grid-cols-3 gap-2">
            <Button
                variant="outline"
                type="button"
                className="w-full"
                onClick={() => onLogin("facebook")}
            >
                <Facebook className="mr-2 h-4 w-4 text-blue-600" />
                Facebook
            </Button>
            <Button
                variant="outline"
                type="button"
                className="w-full"
                onClick={() => onLogin("tiktok")}
            >
                <TikTokIcon className="mr-2 h-4 w-4 text-black dark:text-white" />
                TikTok
            </Button>
            <Button
                variant="outline"
                type="button"
                className="w-full"
                onClick={() => onLogin("apple")}
            >
                <Apple className="mr-2 h-4 w-4 text-black dark:text-white" />
                Apple
            </Button>
        </div>
    )
}
